.horizontalMenu-list {
	>li.rightmenu {
		&:after {
			content: "";
			display: table;
		}
	}
	.sub-menu {
		&:before {
			position: absolute;
			top: -6px;
			left: 12px;
			display: inline-block;
			border-right: 5px solid transparent;
			border-bottom: 5px solid rgba(0, 40, 100, 0.12);
			border-left: 5px solid transparent;
			border-bottom-color: rgba(0, 0, 0, 0.2);
			content: '';
			left: 12px;
			right: auto;
		}
		.sub-menu {
			&:before {
				display: none;
			}
			&:after {
				display: none;
			}
		}
		&:after {
			position: absolute;
			top: -5px;
			left: 12px;
			display: inline-block;
			border-right: 5px solid transparent;
			border-bottom: 5px solid #fff;
			border-left: 5px solid transparent;
			content: '';
			left: 12px;
			right: auto;
		}
	}
	.halfdiv {
		&:before {
			position: absolute;
			top: -6px;
			left: 12px;
			display: inline-block;
			border-right: 5px solid transparent;
			border-bottom: 5px solid rgba(0, 40, 100, 0.12);
			border-left: 5px solid transparent;
			border-bottom-color: rgba(0, 0, 0, 0.2);
			content: '';
			left: 12px;
			right: auto;
		}
		&:after {
			position: absolute;
			top: -5px;
			left: 12px;
			display: inline-block;
			border-right: 5px solid transparent;
			border-bottom: 5px solid #fff;
			border-left: 5px solid transparent;
			content: '';
			left: 12px;
			right: auto;
		}
	}
}
