	.animated-arrow {
		position: absolute;
		left: 0;
		top: 0;
		z-index: 102;
		-webkit-transition: all 0.4s ease-in-out;
		-moz-transition: all 0.4s ease-in-out;
		-o-transition: all 0.4s ease-in-out;
		-ms-transition: all 0.4s ease-in-out;
		transition: all 0.4s ease-in-out;
		cursor: pointer;
		padding: 16px 35px 16px 0px;
		margin: 11px 0 0 15px;
		span {
			cursor: pointer;
			height: 2px;
			width: 17px;
			background: #46484a;
			position: absolute;
			display: block;
			content: '';
			transition: all 500ms ease-in-out;
			&:before {
				cursor: pointer;
				height: 2px;
				width: 17px;
				background: #46484a;
				position: absolute;
				display: block;
				content: '';
				top: -7px;
				width: 23px;
				transition: all 500ms ease-in-out;
			}
			&:after {
				cursor: pointer;
				height: 2px;
				width: 17px;
				background: #46484a;
				position: absolute;
				display: block;
				content: '';
				bottom: -7px;
				width: 17px;
				transition: all 500ms ease-in-out;
			}
		}
	}
	.horizontal-gradient {
